      </a-space>
    </div>
    <a-divider></a-divider>
    <div class="task-container">
      <a-list :grid="{ gutter: 16, column: 4 }">
        <a-list-item v-for="item in list" :key="item.id">
          <router-link target="_blank" :to="{ path: '/training/task/answer' }">
            <a-card hoverable>
              <!-- <img slot="cover" alt="example" src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png" /> -->
