              <i class="fa fa-trash-o" title="删除任务" @click="handleDel(item)"></i>
              <i class="fa" :class="[item.closed ? 'fa-eye' : 'fa-eye-slash']" @click="handleSwitch(item)" :title="item.closed ? '开启任务' : '关闭任务'"></i>
              <router-link target="_blank" :to="{ path: '/training/task/edit' }"><a-icon key="edit" type="edit" title="编辑任务" /></router-link>
