            </template> -->
              <a-card-meta :title="item.name">
                <div slot="description" class="font-xs">
                  <div class="flex items-center v-mb-xs">
                    <span>选手人次：</span>
                    <b>{{ item.people }}</b>
                  </div>
                  <div class="flex items-center v-mb-xs">
