<script>
export default {
  data() {
    let list = []
    for (let index = 1; index < 7; index++) {
      list.push({
        id: index,
        name: '热敏机打印程序设计' + index,
        publishTIme: '2021-04-21 16:' + (50 - index),
        people: 12 + index,
        task: index,
        period: 2.5,
        closed: false,
      })
    }
    return {
      list,
    }
  },
  methods: {
    handleSwitch(item) {
      this.$confirm({
        title: `确认${item.closed ? '开启' : '关闭'}任务【${item.name}】？`,
        // content: 'Some descriptions',
        okText: '确认',
        // okType: 'danger',
        cancelText: '取消',
        onOk() {
          setTimeout(() => {
            item.closed = !item.closed
          }, 500)
        },
        onCancel() {
          console.log('Cancel')
        },
      })
    },
    handleDel(item) {
      this.$confirm({
        title: `确认删除任务【${item.name}】？`,
        // content: 'Some descriptions',
        okText: '确认删除',
        okType: 'danger',
        cancelText: '取消',
        onOk() {
          console.log('OK')
        },
        onCancel() {
          console.log('Cancel')
        },
      })
    },
  },
}
</script>
